@media (max-width: 1000px) {

    .logopri {
        display: none;
    }

    .logoprimob {
        text-align: center;
        display: flex;
        padding: 40px;
        width: 100%;
        max-width: 60.5px;
        min-width: 60.5px;
    }

    .ulheader {
        display: none;
    }


